@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  position: relative;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
body {
  background: #FDFDFD url("../../assets/img/hero-bg-wrapper.png") no-repeat;
  background-size: 100% 100%;
  height: auto;
  background-position: top center;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 2.2rem;
  font-weight: 400;
  color: #252525;
}

.page {
  position: relative;
}

.btn {
  color: #fff;
  background: #1E88E5;
}

a {
  color: #252525;
}

.container {
  max-width: 128rem;
  padding: 0 1rem;
  margin: 0 auto;
}

.header {
  padding-top: 5rem;
  margin-bottom: 12rem;
}
.header__logo {
  width: 23.8rem;
}
.header__inner {
  background: white;
  padding: 1.5rem 2.5rem 1.5rem 2rem;
  border-radius: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__btn {
  color: #fff;
  margin-left: 6rem;
  font-weight: 600;
  font-size: 2rem;
  line-height: 3rem;
  padding: 11px 43px;
  border-radius: 5rem;
}

.menu {
  margin-left: auto;
}
.menu__btn {
  display: none;
}
.menu__list {
  display: flex;
  justify-content: space-between;
}
.menu__list-link {
  font-size: 2rem;
  color: rgba(37, 37, 37, 0.7);
  font-weight: 400;
  line-height: 3rem;
  transition: color 0.3s;
}
.menu__list-link:hover {
  color: #1E88E5;
}
.menu__item + .menu__item {
  margin-left: 3.5rem;
}

.hero__inner {
  padding-bottom: 17.2rem;
  background: url("../../assets/img/hero-bg.png") no-repeat;
  background-position: 100% -99px;
  background-size: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.hero__top {
  max-width: 63.4rem;
  margin-bottom: 8.4rem;
  text-align: left;
}
.hero__title {
  font-weight: 700;
  font-size: 6.5rem;
  line-height: 9.8rem;
  color: #252525;
  margin-bottom: 1.7rem;
}
.hero__subtitle {
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: rgba(37, 37, 37, 0.6);
}
.hero__bottom {
  display: flex;
  align-items: center;
  gap: 1.9rem;
}
.hero__link {
  padding: 2rem 2.4rem;
  border-radius: 1rem;
  font-weight: 600;
  font-size: 2.4rem;
  line-height: 3.6rem;
}
.hero__form {
  position: relative;
  width: 42rem;
  text-align: left;
}
.hero__form-input {
  background: #FFFFFF;
  box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.05);
  padding: 2.3rem 7rem 2.3rem 2.5rem;
  width: 100%;
  height: 7.6rem;
  border-radius: 1rem;
}
.hero__form-input::placeholder {
  font-weight: 400;
  font-size: 2rem;
  line-height: 3rem;
  color: #9D9D9D;
  max-width: 40rem;
}
.hero__form-btn {
  position: absolute;
  right: 1rem;
  top: 1.3rem;
  background: #1E88E5;
  border-radius: 50%;
  padding: 1.4rem;
  width: 5rem;
  height: 5rem;
}

.tutorial {
  margin-bottom: 17.6rem;
}
.tutorial__items {
  display: flex;
  background: #FFFFFF;
  box-shadow: 0 2rem 3rem rgba(0, 0, 0, 0.05);
  border-radius: 1rem;
  padding: 4.1rem 5.3rem;
  justify-content: space-between;
}
.tutorial__item {
  margin: 0 auto;
  max-width: 30rem;
  padding: 0 1rem;
  text-align: center;
}
.tutorial__img-wrapper {
  margin: 0 auto;
  border-radius: 50%;
  padding: 2.5rem;
  margin-bottom: 6rem;
  background: #F2FAFA;
  width: 11rem;
}
.tutorial__title {
  font-weight: 500;
  font-size: 3rem;
  line-height: 4.5rem;
  color: #252525;
}
.tutorial__subtitle {
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #9D9D9D;
}

.find {
  margin-bottom: 17.6rem;
}
.find__inner {
  padding-bottom: 8.5rem;
  background: url("../../assets/img/find-bg-2.png") no-repeat;
  background-position: left;
  background-size: 61.6rem;
  display: flex;
  position: relative;
}
.find__top, .find__center {
  background: #FFFFFF;
  box-shadow: 0 0.1rem 0.6rem rgba(0, 0, 0, 0.1);
  border-radius: 2rem;
  text-align: center;
  padding: 2.7rem 1.6rem;
}
.find__top {
  position: absolute;
  top: 2.4rem;
  left: 29.8rem;
}
.find__number {
  font-weight: 600;
  font-size: 3.6rem;
  line-height: 5.4rem;
  color: #1E88E5;
}
.find__text {
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #9D9D9D;
}
.find__center {
  position: absolute;
  top: 20rem;
  left: 36.9rem;
}
.find__info {
  max-width: 51.6rem;
  margin-left: auto;
  text-align: right;
}
.find__title {
  font-weight: 500;
  font-size: 5.6rem;
  line-height: 8.4rem;
  color: #252525;
  margin-bottom: 2rem;
}
.find__title span {
  color: #1E88E5;
}
.find__info-text {
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #9D9D9D;
}

.work {
  margin-bottom: 7.3rem;
}
.work__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 7.5rem;
}
.work__overtitle {
  font-weight: 400;
  font-size: 36px;
  line-height: 54px;
  color: #9D9D9D;
}
.work__title {
  font-weight: 500;
  font-size: 56px;
  line-height: 84px;
  color: #252525;
}
.work__title span {
  color: #1E88E5;
}
.work__nav button svg {
  transform: translate(0, 10%);
}
.work__arrow-left, .work__arrow-right {
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  border-radius: 50%;
}
.work__arrow-left {
  background: #fff;
  margin-right: 2.5rem;
}
.work__arrow-right {
  background: #1E88E5;
}
.work__items {
  display: grid;
}
.work__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  background: #FFFFFF;
  box-shadow: 0 0.1rem 0.6rem rgba(0, 0, 0, 0.1);
  border-radius: 3rem;
  max-width: 37.2rem;
}
.work__item-logo {
  margin-bottom: 3rem;
}
.work__item-title {
  font-weight: 500;
  font-size: 3.2rem;
  line-height: 4.8rem;
  color: #252525;
  margin-bottom: 0.5rem;
}
.work__item-text {
  text-align: center;
  max-width: 33.1rem;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #9D9D9D;
  margin-bottom: 3rem;
}
.work__item-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.work__item-price {
  font-weight: 500;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: rgba(37, 37, 37, 0.7);
  max-width: 13.7rem;
  text-align: center;
}
.work__item-price span {
  font-size: 2.8rem;
  line-height: 4.2rem;
}
.work__item-link {
  display: inline-block;
  font-weight: 500;
  font-size: 2.4rem;
  line-height: 3.6rem;
  text-decoration-line: underline;
  color: #1E88E5;
}

.footer {
  margin-bottom: 5.6rem;
}
.footer__inner {
  padding: 13.3rem 0 2.8rem;
}
.footer__items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 13rem;
  align-items: start;
  justify-content: space-between;
  margin-bottom: 9.8rem;
}
.footer__item:first-child {
  width: 31.5rem;
}
.footer__item:nth-child(2) {
  width: 15.2rem;
}
.footer__item:nth-child(3) {
  width: 18.4rem;
}
.footer__item:nth-child(4) {
  width: 24.2rem;
}
.footer__logo {
  width: 100%;
  margin-bottom: 0.1rem;
}
.footer__text {
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.7rem;
  color: #252525;
  margin-bottom: 2rem;
}
.footer__social-link + .footer__social-link {
  margin-left: 2.7rem;
}
.footer__title {
  font-weight: 500;
  font-size: 2.4rem;
  line-height: 3.6rem;
  margin-bottom: 4rem;
  color: #252525;
}
.footer__link {
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.7rem;
  color: #252525;
}
.footer__list-link {
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.7rem;
  color: #252525;
}
.footer__list-map, .footer__list-tel, .footer__list-email {
  padding-left: 3.7rem;
  position: relative;
}
.footer__list-map::before {
  content: "";
  background: url("../../assets/img/ico/footer-map.svg");
  position: absolute;
  left: 0;
  width: 2.4rem;
  height: 2.4rem;
}
.footer__list-tel::before {
  content: "";
  background: url("../../assets/img/ico/footer-phone.svg");
  position: absolute;
  left: 0;
  width: 2.4rem;
  height: 2.4rem;
}
.footer__list-email::before {
  content: "";
  background: url("../../assets/img/ico/footer-email.svg");
  position: absolute;
  left: 0;
  width: 2.4rem;
  height: 2.4rem;
}
.footer__rights {
  text-align: center;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.7rem;
  color: #9D9D9D;
}

@media (max-width: 768px) {
  .up {
    display: none;
  }

  .header {
    display: none;
  }
  .header .header__lang {
    display: none;
  }

  .hero__inner {
    padding: 1rem 0 10rem;
  }
  .hero__lang {
    display: none;
  }
  .hero__menu-mobile {
    display: block;
  }
  .hero__photo {
    width: 100%;
    min-height: 39rem;
    background: url(/../assets/img/photo.jpg) no-repeat;
    background-position: 60% 40%;
    background-size: 250%;
  }

  .hero__menu {
    order: 1;
  }
  .hero__menu-btn {
    display: block;
    background: transparent;
    position: fixed;
    top: 2rem;
    right: 1rem;
    z-index: 2;
  }
  .hero__menu-btn--active {
    opacity: 0.3;
  }
  .hero__menu-btn-line {
    display: block;
    width: 100%;
    height: 0.2rem;
    background: black;
    position: absolute;
    left: 0;
    transition: all 0.3s linear;
  }
  .hero__menu-btn-line--first {
    top: 0;
  }
  .hero__menu-btn-line--second, .hero__menu-btn-line--third {
    top: 50%;
    transform: translateY(-50%);
  }
  .hero__menu-btn-line--fourth {
    bottom: 0;
    color: black;
  }
  .hero__menu-btn--active .hero__menu-btn-line--first, .hero__menu-btn--active .hero__menu-btn-line--fourth {
    opacity: 0;
  }
  .hero__menu-btn--active .hero__menu-btn-line--second {
    transform: translateY(-50%) rotate(45deg);
  }
  .hero__menu-btn--active .hero__menu-btn-line--third {
    transform: translateY(-50%) rotate(-45deg);
  }
  .hero__menu-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: fixed;
    text-align: center;
    opacity: 0.95;
    top: 0;
    left: 0;
    right: 0;
    padding-bottom: 2rem;
    background-color: #ffffff;
    transform: translatex(80rem);
    transition: all 0.5s;
  }
  .hero__menu-list.hero__menu-list--active {
    z-index: 1;
    transform: translateY(0);
  }
  .hero__menu-list-link {
    display: block;
    font-size: 1.8rem;
    line-height: 2.8rem;
    padding: 2rem 0;
  }

  .certificates__items {
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (max-width: 576px) {
  .hero__photo {
    background-size: 300%;
  }
  .hero__title {
    font-size: 3rem;
    line-height: 3.4rem;
  }

  .skills__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10rem;
  }
}
@media (max-width: 480px) {
  .hero__top {
    display: block;
  }
  .hero__title {
    margin-bottom: 1rem;
  }
  .hero__text {
    text-align: left;
  }
  .hero__photo {
    background-size: 400%;
  }

  .about__items p {
    font-size: 1.6rem;
  }

  .certificates__items {
    width: 90%;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    margin: 0 auto;
  }

  .social__link:hover {
    transform: none;
  }
  .social__link + .social__link {
    margin-left: auto;
  }

  .footer__bottom {
    font-size: 1.6rem;
  }
}
@media (max-width: 320px) {
  .hero__top {
    flex-wrap: wrap;
  }
  .hero__title {
    margin-bottom: 1rem;
  }
  .hero__menu-mobile {
    order: 2;
  }
  .hero__photo {
    background-size: 500%;
  }

  .about__items p {
    font-size: 1.4rem;
  }

  .certificates__items {
    width: 100%;
  }
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  width: 100%;
  padding: 0.5rem !important;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("./ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("./fonts/slick.eot");
  src: url("./fonts/slick.eot?#iefix") format("embedded-opentype"), url("./fonts/slick.woff") format("woff"), url("./fonts/slick.ttf") format("truetype"), url("./fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}
.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}
.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}
.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}
[dir=rtl] .slick-prev {
  left: auto;
  right: -25px;
}
.slick-prev:before {
  content: "←";
}
[dir=rtl] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}
[dir=rtl] .slick-next {
  left: -25px;
  right: auto;
}
.slick-next:before {
  content: "→";
}
[dir=rtl] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}
.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}
.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}
.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}